import React, { useState } from "react"
import ModalVideo from "react-modal-video"

const Item = props => {
  const [isOpen, setIsOpen] = useState(false)

  return (
    <table className="item-table">
      <tbody>
        <tr>
          <td className="item-name">{props.name || ""}</td>
          <td className="item-when">{props.when || ""}</td>
        </tr>
        <tr>
          <td>
            <a className="item-org" href={props.url}>
              {props.logoUrl && (
                <img src={props.logoUrl} width="40" height="40" />
              )}
              {props.org || ""}
            </a>
          </td>
          <td className="item-where">{props.where || ""}</td>
        </tr>
        <tr>
          <td
            className="item-more-info"
            colSpan={2}
            dangerouslySetInnerHTML={{ __html: props.moreInfo }}
          />
        </tr>
        {props.demoUrl && (
          <tr>
            <ModalVideo
              channel={props.demoUrl.type}
              isOpen={isOpen}
              videoId={props.demoUrl.url}
              url={props.demoUrl.url}
              onClose={() => setIsOpen(false)}
            />
            <td className="item-demo">
              <button
                class="button button-outline"
                onClick={() => setIsOpen(true)}
              >
                <span
                  class="iconify"
                  data-icon="bi:collection-play-fill"
                  data-inline="false"
                ></span>
                PREVIEW
              </button>
            </td>
          </tr>
        )}
        <tr>
          <div className="item-skill-continer">
            {props.skills &&
              props.skills.map(skill => (
                <span className="item-skill">{skill.name}</span>
              ))}
          </div>
        </tr>
      </tbody>
    </table>
  )
}

export default Item
